/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type=search] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */
audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
  max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */
[hidden] {
  display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */
html {
  font-size: 100%;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  -ms-text-size-adjust: 100%;
  /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */
a:focus {
  outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */
a:active,
a:hover {
  outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */
img {
  border: 0;
  /* 1 */
  -ms-interpolation-mode: bicubic;
  /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */
figure {
  margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */
form {
  margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */
legend {
  border: 0;
  /* 1 */
  padding: 0;
  white-space: normal;
  /* 2 */
  *margin-left: -7px;
  /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */
button,
input,
select,
textarea {
  font-size: 100%;
  /* 1 */
  margin: 0;
  /* 2 */
  vertical-align: baseline;
  /* 3 */
  *vertical-align: middle;
  /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */
button,
input {
  line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */
button,
select {
  text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */
button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */
  *overflow: visible;
  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */
button[disabled],
html input[disabled] {
  cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */
input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
  *height: 13px;
  /* 3 */
  *width: 13px;
  /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */
input[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  /* 2 */
  box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */
textarea {
  overflow: auto;
  /* 1 */
  vertical-align: top;
  /* 2 */
}

/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
button,
input,
select,
textarea {
  color: #222;
}

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

img {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

* {
  box-sizing: border-box;
}

body a {
  text-decoration: none;
  display: inline-block;
}
body h1 {
  font-family: "Raleway", sans-serif;
  font-size: 2rem;
  color: black;
  font-weight: 700;
  line-height: 150%;
  text-align: center;
}
@media screen and (min-width: 1000px) {
  body h1 {
    text-align: left;
  }
}
body p {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  color: black;
  font-weight: 400;
  line-height: 150%;
  margin: 1rem 0;
  text-align: center;
}
@media screen and (min-width: 1000px) {
  body p {
    text-align: left;
  }
}

form {
  margin: 3rem 0;
}
form input {
  outline: none;
  border: 1px solid grey;
  border-radius: 5px;
  height: 44px;
  padding: 0 1rem;
  box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.1);
  display: block;
}
form input::placeholder {
  color: #bfbfbf;
}
form input:focus {
  border: 2px solid #3065f8;
}
form [type=submit] {
  font-family: "Raleway", sans-serif;
  color: white;
  font-weight: 700;
  display: block;
  text-decoration: none;
  font-size: 0.9rem;
  background-color: #3065f8;
  padding: 0.8rem 3rem;
  border: none;
  border-radius: 5px;
  box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.1);
  margin: 1rem 0;
}
form [type=submit]:hover {
  background-color: #6e93fa;
}

.container {
  width: 90%;
  margin: 0 auto;
}
@media screen and (min-width: 800px) {
  .container {
    width: 80%;
  }
}
@media screen and (min-width: 1000px) {
  .container {
    max-width: 1080px;
  }
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem 0;
}
header .logo {
  width: 8rem;
}
header ul {
  display: flex;
}
header ul a {
  font-family: "Raleway", sans-serif;
  font-size: 1rem;
  color: black;
  font-weight: 400;
  line-height: 150%;
  margin-left: 1rem;
}
@media screen and (min-width: 600px) {
  header ul a {
    margin-left: 3rem;
  }
}

.intro {
  padding-bottom: 6rem;
}
@media screen and (min-width: 1000px) {
  .intro {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
  }
}
.intro img {
  max-width: 100%;
  margin-bottom: 4rem;
}
@media screen and (min-width: 1000px) {
  .intro img {
    max-width: 45%;
    height: fit-content;
  }
}
@media screen and (min-width: 1000px) {
  .intro div {
    max-width: 45%;
  }
}
@media screen and (min-width: 1000px) {
  .intro div form {
    display: flex;
    margin: 0;
  }
}
.intro div form input,
.intro div form button {
  width: 100%;
  margin: 1rem auto;
}
@media screen and (min-width: 1000px) {
  .intro div form input,
.intro div form button {
    margin: 1rem 0;
    margin-right: 1rem;
  }
}
@media screen and (min-width: 1000px) {
  .intro div form input {
    width: 50%;
  }
}
@media screen and (min-width: 1000px) {
  .intro div form button {
    width: 40%;
    padding: 0.8rem 1rem;
  }
}

.info {
  background-color: #f6f6fe;
  position: relative;
  padding-top: 3rem;
  padding-bottom: 6rem;
  text-align: center;
}
@media screen and (min-width: 1000px) {
  .info > div.container {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
  }
}
.info > div.container > img {
  max-width: 100%;
  margin-bottom: 4rem;
}
@media screen and (min-width: 1000px) {
  .info > div.container > img {
    max-width: 45%;
    height: fit-content;
  }
}
@media screen and (min-width: 1000px) {
  .info > div.container > div {
    max-width: 50%;
    text-align: left;
  }
}
.info::before {
  content: "";
  background-image: url(../images/bg-curve-mobile.svg);
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  width: 100%;
  height: 10vw;
  top: -10vw;
  left: 0;
}
@media screen and (min-width: 1000px) {
  .info::before {
    background-image: url(../images/bg-curve-desktop.svg);
    width: 100%;
    height: 5vw;
    top: -5vw;
  }
}
.info p {
  text-align: left;
}
.info a {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  color: black;
  font-weight: 400;
  line-height: 150%;
  color: #3c9f8f;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #3c9f8f;
}
.info a::after {
  content: "";
  display: inline-block;
  background-image: url(../images/icon-arrow.svg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 1.5rem;
  height: 1.5rem;
  position: relative;
  top: 0.4rem;
  margin-left: 1rem;
}
.info a:hover {
  color: #6bc7b8;
  border-bottom: 1px solid #6bc7b8;
}
.info a:hover::after {
  opacity: 70%;
}
.info .testimonials {
  background-color: white;
  border-radius: 5px;
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.1);
  max-width: 80%;
  padding: 2rem;
  margin: 0 auto;
  margin-top: 3rem;
}
@media screen and (min-width: 1000px) {
  .info .testimonials {
    margin: 0;
    margin-top: 3rem;
    max-width: 70%;
  }
}
.info .testimonials cite {
  font-family: "Open Sans", sans-serif;
  font-size: 0.8rem;
  color: black;
  font-weight: 400;
  line-height: 150%;
  text-align: left;
  display: block;
  margin-bottom: 1rem;
}
.info .testimonials cite::before {
  content: "";
  background-image: url(../images/icon-quotes.svg);
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  height: 1rem;
  width: 1rem;
  margin-bottom: 1rem;
}
.info .testimonials img {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  float: left;
  margin-right: 1rem;
}
.info .testimonials strong {
  font-family: "Raleway", sans-serif;
  font-size: 1rem;
  color: black;
  font-weight: 700;
  line-height: 150%;
  text-align: left;
  letter-spacing: 1px;
  display: block;
  padding-top: 0.2rem;
}
.info .testimonials span {
  font-family: "Open Sans", sans-serif;
  font-size: 0.7rem;
  color: black;
  font-weight: 400;
  line-height: 150%;
  text-align: left;
  display: block;
}

.sign_up {
  background-color: #585989;
  padding: 4rem 0 3rem 0;
}
@media screen and (min-width: 1000px) {
  .sign_up > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media screen and (min-width: 1000px) {
  .sign_up > div > div {
    width: 50%;
  }
}
.sign_up > div > div h1,
.sign_up > div > div p {
  color: white;
}
@media screen and (min-width: 1000px) {
  .sign_up > div form {
    width: 40%;
  }
}
.sign_up > div form input,
.sign_up > div form button {
  width: 70%;
  margin: 1rem auto;
}
@media screen and (min-width: 1000px) {
  .sign_up > div form input {
    width: 100%;
  }
}
@media screen and (min-width: 1000px) {
  .sign_up > div form button {
    width: 80%;
    margin: 1rem 0;
  }
}
@media screen and (min-width: 1240px) {
  .sign_up > div form button {
    width: 60%;
  }
}

footer {
  background-color: #070439;
  padding-top: 4rem;
}
footer img {
  margin-bottom: 2rem;
  width: 12rem;
}
@media screen and (min-width: 1000px) {
  footer div:first-child > div {
    display: flex;
    justify-content: space-between;
  }
}
footer p {
  color: white;
  text-align: left;
  padding-left: 3rem;
  position: relative;
}
footer p.phone::before {
  content: "";
  display: block;
  background-image: url(../images/icon-phone.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  top: 0;
  left: 0;
}
footer p.mail::before {
  content: "";
  display: block;
  background-image: url(../images/icon-email.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  top: 0;
  left: 0;
}
footer ul {
  margin: 3rem 0;
}
@media screen and (min-width: 1000px) {
  footer ul {
    margin: 0;
    margin-bottom: 3rem;
  }
}
footer ul a {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  color: black;
  font-weight: 400;
  line-height: 150%;
  color: white;
  display: inline-block;
  margin: 0.5rem 0;
}
footer ul a:hover {
  color: #585989;
}
footer .icon {
  display: flex;
  justify-content: center;
  margin-bottom: 3rem;
}
footer .icon a {
  height: 3rem;
  width: 3rem;
  border: 1px solid white;
  border-radius: 50%;
  text-align: center;
  margin: 0 0.5rem;
}
footer .icon a i {
  color: white;
  font-size: 1.2rem;
  margin: 0.8rem 0;
}
footer .icon a:hover {
  border: 1px solid #585989;
}
footer .icon a:hover i {
  color: #585989;
}
footer .attribution {
  padding: 1rem 0;
  margin: 0;
  font-size: 10px;
  text-align: center;
}
footer .attribution a {
  color: #3e52a3;
}